.details {
    width: 300px;
  }
  
  .details-img {
    display: block;
    width: 300px;
    height: 300px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }
  
  .details-img-loader {
    display: block;
    width: 300px;
    height: 300px;
    background-color: #e5e5e5;
  }
  
  .details-text {
    border: 1px solid #ccc;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    /* border-top: none; */
  }
  
  .details-text-item {
    padding: 12px 24px;
    border-bottom: 1px solid #ccc;
  }
  
  .details-text-item:last-child{
    border-bottom: none;
  }
  
  .details-text-title {
    font-size: 20px;
    padding-top: 6px;
    padding-bottom: 24px;
  }

  .wrapper{
    margin-top: 120px;
    margin-left: 50px;
  }