.list-container {
    list-style-type: none;
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 6px;
    margin-right: 32px;
  }
  
  .list-item {
    padding: 10px 20px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
    transition: background-color .2s ease;
  }
  
  .list-item:last-child {
    border-bottom: none;
  }
  
  .list-item:hover {
    background-color: #d8d8d8;
  }
  
  .selected {
    background-color: #666;
    color: #fff;
    cursor: auto;
  }
  
  .selected:hover {
    background-color: #666;
  }